    top: 100%;
    left: 0;
    color: #aaaaaa;
    text-transform: uppercase;
    padding-top: 5px; }
    @media screen and (max-width: 991.98px) {
      .heading-style-left span {
        left: 50%;
        transform: translateX(-50%);
        white-space: nowrap; } }
  .heading-style-left div + div .highlight-underline {
